  <template>
    <div class="identify-valid">
      <pwd-crumbs></pwd-crumbs>
      <form action="" @submit.prevent="submit" class="comForm">
        <div class="input-wrapper">
          <input type="text" v-model="sendForm.idNum" :class="errorIdNum?'active':''"  @focus="focus($event,'idNum')" @blur="blur($event,'idNum')" placeholder="身份证号">
          <i class="del" v-show="sendForm.idNum" @mousedown.stop="del('idNum')" :style="delImg"></i>
          <span class="error" >{{errorIdNum}}</span>
        </div>
        <div class="input-wrapper">
          <input type="text" v-model="sendForm.queryName"  @focus="focus($event)" @blur="blur($event)" placeholder="姓名">
          <i class="del" v-show="sendForm.queryName" @mousedown.stop="del('queryName')" :style="delImg"></i>
        </div>
        <div class="submit-wrapper">
          <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
          <input type="submit" value="下一步" class="submit" :class="allValid? 'active':''" :disabled="!allValid">
        </div>
        <div class="go-login-tips">
          已有账号，马上<router-link tag="span" to="/smsLogin" class="register-btn"><span>登录</span></router-link>
        </div>
      </form>
    </div>
  </template>

<script>
import pwdCrumbs from '@/components/password/ThePwdCrumbs'
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { ValidateUtil } from '@/assets/js/global'
import { idNumErrorTips } from '@/assets/js/errorTips'
export default {
  name: 'TheIdentifyVailid',
  mixins: [getComFormMixin],
  data () {
    return {
      sendForm: {
        idNum: '',
        queryName: ''
      }
    }
  },
  mounted () {
    this.getNormalStep()
  },
  methods: {
    submit () {
      // commit('SET_FORGETPWDSTEP')
      let postData = {
        idNum: this.sendForm.idNum,
        name: this.sendForm.queryName
      }
      this.$api.user.identifyValidCommit(postData).then(res => {
        if (res.code === 1) {
          this.$router.push('/forgetPassword/resetPassword')
        } else {
          this.errorTips = res.message
        }
      })
    },
    getMessageCode () {
      if (this.errorIdNum) return
      if (!this.sendForm.idNum) {
        this.errorIdNum = idNumErrorTips.notNull
        return
      }
      this.readsecond()
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'idNum':
          this._validIdNum()
          break
            // case 'pwd':
            //   this.errorPwd = ValidateUtil.pwdRegexs(this.pwdLoginForm.pwd)
            //   break
      }
    },
    getNormalStep () {
      this.$api.user.isNormalByStep1().then(res => {
        if (res.code === 0) {
          this.$router.push({
            path: '/forgetPassword/mobileValid'
          })
        }
      })
    },
    _validIdNum () {
      if (this.sendForm.idNum) {
        if (!ValidateUtil.idCardRegexs(this.sendForm.idNum).pass) {
          this.errorIdNum = ValidateUtil.idCardRegexs(this.sendForm.idNum).tip
        }
      }
    }
  },
  computed: {
    allValid () {
      return this.sendForm.idNum && this.sendForm.queryName && !this.errorIdNum
    }
  },
  components: {
    pwdCrumbs
  }

}
</script>

  <style scoped lang="scss">
    @import "@/assets/styles/comForm.scss";
    .identify-valid{
      background: #fff;
      min-height: 100%;
    }
  </style>
